import { createSearchProduct } from "./SearchProduct";
import { within, userEvent } from "@storybook/testing-library";

export default {
  title: "Component/SearchProduct",
  tags: ["autodocs"],
  parameters: {
    layout: "centered",
  },
  argsTypes: {
    placeholder: { control: "text" },
    fontSize: { control: "text" },
    searchProductBackgroundColor: { control: "color" },
  },
};

export const Default = {
  args: {
    placeholder: "Search here ...",
    fontSize: "12px",
    searchProductBackgroundColor: "#ecc2c2",
  },
  render: (args) => createSearchProduct(args),
};
Default.play = async ({ canvasElement }) => {
  const canvas = within(canvasElement);

  // Test the search functionality
  const searchInput = canvas.getByPlaceholderText("Search here ...");
  userEvent.type(searchInput, "Tesla");

  // Check if filtered products are displayed
  expect(canvas.getByText("Tesla Model S")).toBeInTheDocument();
  expect(canvas.queryByText("Ford Mustang")).not.toBeInTheDocument();

  // Test the filter popup
  const filterIcon = canvas.getByRole("img", { name: /filter/i });
  userEvent.click(filterIcon);

  // Check if filter popup contains brand options
  expect(canvas.getByText("Tesla")).toBeInTheDocument();

  // Click on a brand to filter
  userEvent.click(canvas.getByText("Tesla"));

  // Check if only Tesla products are displayed
  expect(canvas.getByText("Tesla Model S")).toBeInTheDocument();
  expect(canvas.queryByText("Ford Mustang")).not.toBeInTheDocument();

  // Clear filter
  userEvent.click(canvas.getByText("Clear Filter"));

  // Check if all products are displayed again
  expect(canvas.getByText("Ford Mustang")).toBeInTheDocument();
};
;export const __namedExportsOrder = ["Default"];