___CSS_LOADER_EXPORT___.push([module.id, `.search-product {
  width: fit-content;
  height: auto;
  background-color: rgb(156, 105, 105);
  box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
}
.search {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  width: fit-content;
  height: auto;
  padding: 14px 24px;
  position: relative;
}

.search input {
  border-radius: 5px;
  padding: 10px;
}

.search i {
  color: #fff;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
}
.filter-popup {
  width: fit-content;
  /* height: 25vh; */
  background-color: #888686;
  position: absolute;
  top: 56%;
  right: 10%;
  overflow: hidden;
  overflow-y: auto;
  list-style-type: none;
}

.filter-popup li {
  text-decoration: none;
  cursor: pointer;
}

.filter-popup li:hover {
  background-color: #b45555;
}

.filter-popup li span {
  font-size: 12px;
  color: #fff;
}

/* .filter-popup {
  width: fit-content;
  height: 20vh;
  background-color: #888686;
  position: absolute;
  top: 0;
  right: 0;
  overflow: hidden;
  overflow-y: auto;
  list-style-type: none;
} */

.list {
  width: fit-content;
  height: auto;
}

.product-list {
  list-style: none;
  padding: 0;
  margin: 0;
  border-radius: 5px;
}

.product-list li {
  padding: 10px;
  cursor: pointer;
}

.product-list li:hover {
  border-bottom: none;
  background-color: rgb(135, 172, 159);
  border-radius: 5px;
}
